import { ChartUnit } from '@loopring-web/component-lib/static-resource'

import { ScaleAreaChart, ToggleButtonGroup, useSettings } from '@loopring-web/component-lib'
import { Box, Grid } from "@material-ui/core"
import { WithTranslation } from 'react-i18next'
import { TradeTitle } from '@loopring-web/component-lib/components/block'
import { UpColor } from '@loopring-web/component-lib/src/static-resource';
import { useBasicInfo } from './hook'

const BasicInfoPanel = ({props, coinAInfo, coinBInfo, tradeFloat, marketArray, t, ...rest}: any & WithTranslation) => {

    const {
        // change,
        chartType,
        tgItemJSXs,
        tgItemJSXsPriceChart,
        handleChange,
        // originData,
    } = useBasicInfo(props, t)
    const {upColor} = useSettings();


    return <>
        <Grid item xs={8}>
            <TradeTitle {...{ coinAInfo, coinBInfo,
                ...rest, t, tradeFloat
            }}></TradeTitle>
        </Grid>
        <Grid item xs={4} display={'flex'} justifyContent={'flex-end'} alignItems={'flex-end'}>
        <ToggleButtonGroup exclusive {...{...rest, t, tgItemJSXs, value: chartType}}
        handleChange={handleChange}/>
        </Grid>

        <Grid item xs={12}>
            <Box minHeight={256} maxHeight={256} display={'block'} style={{height: '100%', width: '100%'}}>
                <ScaleAreaChart type={chartType} data={marketArray ?? []} riseColor={upColor as keyof typeof UpColor}
                                handleMove={() => {
                                }}/>
            </Box>
        </Grid>
        <Grid item xs={12} height={24} display={'flex'} justifyContent={'flex-end'}>
            <ToggleButtonGroup exclusive {...{
                ...rest, t, tgItemJSXs: tgItemJSXsPriceChart,
                value: ChartUnit.D1, size: 'small'
            }}
                               handleChange={() => undefined}/>
        </Grid>
    </>

};

export default BasicInfoPanel
