import { StateBase } from '../interface';
import { ChainId } from 'loopring-sdk';

export enum ENV {
    DEV='DEV',
    UAT='UAT',
    PROD='PROD',
}
export enum NETWORKEXTEND {
    NONETWORK = 'unknown'
}
export type NETWORK = NETWORKEXTEND | ChainId


export type System = {
    env:keyof typeof ENV,
    chainId: 1|5|'unknown'
    // network: keyof typeof NETWORK,
    baseURL: string,
    faitPrices:any | undefined,
    gasPrice:number | undefined,
    forex:number | undefined,


}

export type SystemStatus = System & {
    // system:System | {}
    __timer__:NodeJS.Timeout | -1
}  & StateBase



