const getAmmActivityMapApi = async () => {
    const data:AmmActivity<any> = {
        // @ts-ignore
        'AMM-LRC-ETH': {
            totalRewards: 241232132,
            myRewards: 1232.123,
            rewardToken: coinMap.ETH as CoinInfo<CoinType>,
            duration: {
                from: new Date('2021-1-1'),
                to: new Date()
            },
        }, 'AMM-ETH-USDC': {
            totalRewards: 241232132,
            myRewards: 1232.123,
            rewardToken: coinMap.ETH as CoinInfo<CoinType>,
            duration: {
                from: new Date('2021-1-1'),
                to: new Date()
            },
        }, 'AMM-ETH-USDT': {
            totalRewards: 241232132,
            myRewards: 1232.123,
            rewardToken: coinMap.ETH as CoinInfo<CoinType>,
            duration: {
                from: new Date('2021-1-1'),
                to: new Date()
            },
        }, 'AMM-LRC-USDT': {
            totalRewards: 241232132,
            myRewards: 1232.123,
            rewardToken: coinMap.ETH as CoinInfo<CoinType>,
            duration: {
                from: new Date('2021-1-1'),
                to: new Date()
            },
        }
    }
    return  Promise.resolve({data})
}

export function* getPostsSaga() {
    try {
        //
        const { data } = yield call(getAmmActivityMapApi);
        yield put(getAmmActivityMapStatus({ammActivityMap:data}));
    } catch (err) {
        yield put(getAmmActivityMapStatus(err));
    }
}

export default function* ammActivityMapSaga() {
    yield all([takeLatest(getAmmActivityMap, getPostsSaga)]);
}
