export const LiquidityOverview = withTranslation('common')(({ t, ...rest }: WithTranslation) => {
    const [chartPeriod, setChartPeriod] = React.useState('ALL');
    const [page, setPage] = React.useState(  1);
    const {currency} = useSettings();
    const {totalDollar,totalYuan,totalFeeDollar,totalFeeYuan, totalMiningDollar, totalMiningYuan} = {
        totalDollar :1000,
        totalYuan:33,
        totalFeeDollar:323,totalFeeYuan:12, totalMiningDollar:232, totalMiningYuan:35}

    const _handlePageChange = React.useCallback((page: number) => {
        setPage(page);
        // updateData({actionType: ActionType.page, currPage: page})
    }, [])
    return (
        <>
            <StyleWrapper container  spacing={1} padding={3} margin={0} >
                <Grid display={'flex'} flexDirection={'column'} justifyContent={'space-around'} item sm={3}>
                    <Typography variant={'body1'} component={'h5'} color={'textSecondary'}>{t('labelTotalPositionValue')}</Typography>
                    <Typography variant={'h4'} marginTop={1}>
                        {totalDollar === undefined ? 'N/A' : currency === Currency.dollar ?  PriceTag.Dollar + getThousandFormattedNumbers(totalDollar)
                            :  PriceTag.Yuan + getThousandFormattedNumbers(totalYuan as number)}
                    </Typography>
                </Grid>
                <Grid display={'flex'} flexDirection={'column'} justifyContent={'space-around'} item sm={3}>
                    <Typography variant={'body1'} component={'h5'} color={'textSecondary'}>{t('labelFeeRewards')}</Typography>
                    <Typography variant={'h4'} marginTop={1}>
                        {totalFeeDollar === undefined ? 'N/A' : currency === Currency.dollar ?  PriceTag.Dollar + getThousandFormattedNumbers(totalFeeDollar)
                            :  PriceTag.Yuan + getThousandFormattedNumbers(totalFeeYuan as number)}
                    </Typography>
                </Grid>
                <Grid display={'flex'} flexDirection={'column'} justifyContent={'space-around'} item sm={3}>
                    <Typography variant={'body1'} component={'h5'} color={'textSecondary'}>{t('labelMiningRewards')}</Typography>
                    <Typography variant={'h4'} marginTop={1}>
                        {totalMiningDollar === undefined ? 'N/A' : currency === Currency.dollar ?  PriceTag.Dollar + getThousandFormattedNumbers(totalMiningDollar)
                            :  PriceTag.Yuan + getThousandFormattedNumbers(totalMiningYuan as number)}
                    </Typography>
                </Grid>
            </StyleWrapper>
            <StyleWrapper container marginY={2} height={340} >
                    <StyledBtnGroupWrapper display={'flex'} padding={3} justifyContent={'space-between'}>
                        <Typography variant={'body1'} component={'h5'} color={'textSecondary'}>{t('labelLiquidityValue')}</Typography>
                        <Box marginRight={-1}>
                            <ToggleButtonGroup  exclusive size="small" {...{ ...rest, t, data: toggleData, value: chartPeriod, setValue: setChartPeriod}} />
                        </Box>
                    </StyledBtnGroupWrapper>
                    <ScaleAreaChart type={ChartType.Trend} data={[]} />
            </StyleWrapper>
            <Typography paddingLeft={2} variant={'h4'} >{t('labelMyAmm')}</Typography>
            <TableWrapStyled container marginY={2} paddingY={2} flex={1} height={568}>
                <Grid item xs={12} display={'flex'} flexDirection={'column'} >
                    <MyPoolTable

                        rawData={rawData}
                        pagination= {{pageSize: 5}}
                        handleDeposit={(row:any)=>{console.log()}}
                        handleWithdraw={(row:any)=>{console.log()}}
                        handlePageChange={()=>{}}
                    />
                </Grid>
            </TableWrapStyled>
            <Typography paddingLeft={2} variant={'h4'} >{t('labelMyAmmRecord')}</Typography>
            <TableWrapStyled container marginY={2} paddingY={2} flex={1}>
                <Grid item xs={12} display={'flex'} flexDirection={'column'}>
                    <AmmRecordTable rawData={ammRecordArray} handlePageChange={_handlePageChange} page={page} />
                </Grid>
            </TableWrapStyled>
        </>
    )
})