import { AmmPanel, AmmPanelType, AmmProps } from '@loopring-web/component-lib';
import { AmmData, AmmInData, CoinInfo, IBData } from '@loopring-web/component-lib/static-resource';
import { useAmmPanel } from './hooks';
import React from 'react';

export const AmmPanelView = <T extends AmmData<C extends IBData<I> ? C : IBData<I>>, I,
    ACD extends AmmInData<I>,
    C = IBData<I>>({pair,ammType,...rest}: { pair:{coinAInfo: CoinInfo<C> | undefined, coinBInfo: CoinInfo<C> | undefined},ammType?:keyof typeof AmmPanelType} & any) => {
    const {ammCalcData,
        ammDepositData,
        ammWithdrawData,
        handleDepositEvent,
        handleWithDrawEvent,
        onAmmWithdrawClick,
        onAmmDepositClick} = useAmmPanel({pair, ammType:ammType?ammType:AmmPanelType.Deposit})

    // const [index, setIndex] = React.useState(AmmPanelTypeMap[ tabSelected ]);

    return <>
        <AmmPanel {...{...rest}}
                  ammWithdrawData={ammWithdrawData}
                  ammDepositData={ammDepositData}
                  ammCalcData={ammCalcData}
                  handleDepositEvent={handleDepositEvent}
                  handleWithDrawEvent={handleWithDrawEvent}
                  onAmmWithdrawClick={onAmmWithdrawClick}
                  onAmmDepositClick={onAmmDepositClick}
                  tabSelected = {ammType?ammType:AmmPanelType.Deposit}

        />
    </>

}