import { createSlice, PayloadAction, Slice } from '@reduxjs/toolkit'
import {  AmmRecordStates } from './interface';
import { STATUS } from '../../constant';

const initialState:Required<AmmRecordStates>  = {
    ammRecords:[],
    status:'UNSET',
    errorMessage:null,
}
const ammRecordSlice:Slice = createSlice({
    name: 'ammRecord',
    initialState,
    reducers: {
        getAmmRecord(state, action:PayloadAction<string | undefined>) {
            state.status = STATUS.PENDING
        },
        getAmmRecordStatus(state, action: PayloadAction<AmmRecordStates>) {
            // @ts-ignore
            if (action.error) {
                state.status =  STATUS.ERROR
                // @ts-ignore
                state.errorMessage = action.error
            }
            state.ammRecords = {...action.payload.ammRecords};
            state.status = STATUS.DONE
        },
        statusUnset: state => {
            state.status = STATUS.UNSET
        }
    },
});
export { ammRecordSlice };
export const { getAmmRecord, getAmmRecordStatus,statusUnset } = ammRecordSlice.actions
