import { all, takeLatest, call, put } from "redux-saga/effects";
import { getAmmRecord, getAmmRecordStatus } from './reducer';

const getAmmRecordApi= ()=>new Promise((resolve)=>{ resolve({data:[
        {side: 'Buy',
            amount: {
                sell:{ belong:'ETH', tradeValue: 0.1 },
                buy:{ belong:'LRC', tradeValue: 123 }
            },
            time: Date.now(),
            fee: 0.02,
            amountLP:{
                sell:{ belong:'ETH', tradeValue: 0.1 },
                buy:{ belong:'LRC', tradeValue: 123 }
            }
        },{side: 'Buy',
            amount: {
                sell:{ belong:'ETH', tradeValue: 0.1 },
                buy:{ belong:'LRC', tradeValue: 123 }
            },
            time: Date.now(),
            fee: 0.02,
            amountLP:{
                sell:{ belong:'ETH', tradeValue: 0.1 },
                buy:{ belong:'LRC', tradeValue: 123 }
            }
        }
    ]})});
export function* getPostsSaga() {
    try {
        //
        const { data } = yield call(getAmmRecordApi);
        yield put(getAmmRecordStatus({ammRecords: data}));
    } catch (err) {
        yield put(getAmmRecordStatus(err));
    }
}

export default function* ammRecordSaga() {
    yield all([takeLatest(getAmmRecord, getPostsSaga)]);
}




