import { AmmProps } from '@loopring-web/component-lib';
import React from 'react';
import { AmmData, AmmInData, IBData } from '@loopring-web/component-lib/static-resource';
import { AmmCard } from '@loopring-web/component-lib/components/block/AmmCard';
import { Grid } from '@material-ui/core';
import { AmmDetail, FloatTag } from '@loopring-web/component-lib/src/static-resource';
import { useAmmMiningUI } from './hook';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { withTranslation } from 'react-i18next';

const AmmCardWrap = React.memo(React.forwardRef((props,ref) => {
    return <AmmCard ref={ref} {...props} ></AmmCard>
}))  as React.ComponentType<AmmDetail<IBData<any>>>;

export const AmmMiningView =  withTranslation('common')(<T extends AmmData<C extends IBData<I> ? C : IBData<I>>, I,
    ACD extends AmmInData<I>,
    C = IBData<I>>({ammProps,t,...rest}: { ammProps:AmmProps<T ,I,ACD> } & any) => {
    const {ammActivities} = useAmmMiningUI();
    const [value,setValue] = React.useState<0|1>(0);
    const handleChange = (event: any, newValue: 0|1) => {
        setValue(newValue);
    }
    return <Grid container spacing={2}>
       <Grid item xs={12}>
           <Tabs value={value}
                 onChange={handleChange}
                 aria-label="tabs switch">
               <Tab label={t('labelCurrentActivities')}  />
               <Tab label={t('labelPastActivities')}  />
           </Tabs>
       </Grid>

        {ammActivities.map((item,index)=>{
            return  <Grid item xs={12} sm={6} lg={4}  key={index}>
                {/*<AmmCardWrap {...item}/>*/}
            </Grid>
        }) }

    </Grid>
})


