import React from "react";
import { AmmData, CoinInfo, IBData, TradeCalcData } from "@loopring-web/component-lib/static-resource";
import { useCustomDCEffect } from "hooks/common/useCustomDCEffect";
import { ammpoolAPI, exchangeAPI } from "stores/apis/api";
import { useTokenMap } from "stores/token";
import { useAccount } from "stores/account/hook";
import { useRouteMatch } from 'react-router';
import { useAmmMap } from '../../../stores/Amm/AmmMap';
import { useWalletLayer2 } from '../../../stores/walletLayer2';
import { TradeFloat } from '@loopring-web/component-lib/src/static-resource';
import { useOpenModals } from '@loopring-web/component-lib/index';
import { useModals } from '../../../hooks/modal/useModals';
import { coinPairInit } from '../../../hooks/help/coinPairInit';
import { updateWallet } from '../../../hooks/help/updateWallet';
import { pairTitleBlock, pairTitleDone } from '../../../hooks/help/pairTitleBlock';
import { getExistedMarket } from 'loopring-sdk';
import { makeTickView } from '../../../hooks/help/makeTickView';

export const useCoinPair = <C extends { [ key: string ]: any }>() => {
    const exchangeApi = exchangeAPI();
    const match: any = useRouteMatch(":symbol")
    const {coinMap, tokenMap, marketArray, marketCoins} = useTokenMap()
    const {ammMap} = useAmmMap();
    const _walletLayer2 = useWalletLayer2();

    const [tradeFloat, setTradeFloat] = React.useState<TradeFloat | undefined>(undefined);
    const [pair, setPair] = React.useState<{ coinAInfo: CoinInfo<C> | undefined, coinBInfo: CoinInfo<C> | undefined }>({
        coinAInfo: undefined,
        coinBInfo: undefined,
    });

    React.useEffect(() => {
        const coinKey = match?.params.symbol ?? undefined;
        let _tradeCalcData: Partial<TradeCalcData<C>> = {}
        let _tradeFloat: Partial<TradeFloat> = {}
        // let _tradeArray: Array<Partial<RawDataTradeItem>> | undefined = undefined;
        _tradeCalcData = coinPairInit({coinKey, _tradeCalcData, tokenMap, coinMap})
        if (_walletLayer2 && _walletLayer2.walletLayer2) {
            const {walletMap} = updateWallet();
            _tradeCalcData.walletMap = walletMap;
        }
        let {
            amm,
            market
        } = getExistedMarket(marketArray, _tradeCalcData.coinSell as string, _tradeCalcData.coinBuy as string);
        //TODO wallet saga done
        let apiList = [];
        if (amm && market && ammMap) {
            // let pairPromise =  usePairTitleBlock({market})
            apiList = [
                pairTitleBlock({coinKey: market, ammKey: amm, ammMap})
            ];
            Promise.all([...apiList]).then(
                ([{ammpoolsbalance, tickMap}]: any[]) => {
                    if (tokenMap) {
                        let {_tradeCalcData: _td, _pair} = pairTitleDone({
                            market,
                            ammpoolsbalance,
                            tokenMap,
                            _tradeCalcData,
                            coinMap,
                            marketCoins
                        })
                        _tradeCalcData = _td;
                        _tradeFloat = makeTickView(tickMap[ market ])
                        setTradeFloat(_tradeFloat as TradeFloat);
                        setPair(_pair)
                    }
                })
        }

    }, []);
    //TODO tickMap
    React.useEffect(() => {
    }, [])

    useCustomDCEffect(async () => {

    }, [ammpoolAPI])
    return {
        // ammCalcData,
        // ammDepositData,
        // ammWithdrawData,
        pair,
        tradeFloat,
        // onSwapClick,
        // ammBtnI18nKey,
        // handleSwapPanelEvent
        // handleDepositEvent,
        // handleWithDrawEvent,
        // onAmmWithdrawClick,
        // onAmmDepositClick
    }
}