import styled from '@emotion/styled/macro'

import { MarketBlock, QuoteTable, TablePaddingX } from '@loopring-web/component-lib'

import { WithTranslation, withTranslation } from 'react-i18next'
import { coinMap } from '@loopring-web/component-lib/static'
import { FloatTag } from '@loopring-web/component-lib/src/static-resource'
import { Box, Grid } from '@material-ui/core'
import { useQuote } from './hook'

const  RowStyled = styled(Grid)`
      & .MuiGrid-root:not(:last-of-type) > div{
        margin-right: ${({theme}) => theme.unit * 3}px;
      }
` as typeof Grid
const  TableWrapStyled = styled(Grid)`
  &{
    background-color: ${({theme}) => theme.colorBase.background().default};
    ${({theme}) => theme.border.defaultFrame({c_key: 'blur', d_R: 1})};
    .rdg{
      height: 100%;
    }
  }
  ${({theme}) => TablePaddingX({pLeft:theme.unit * 3,pRight:theme.unit * 3})}
` as typeof Grid

const QuotePage = withTranslation('common')((rest: WithTranslation) => {

    const { tickList, big4 } = useQuote()

    const formattedRawData = tickList.map((o: any) => ({
        pair: !!o[0].length ? {
            first: o[0][0],
            last: o[0][1]
        }: {
            first: '--',
            last: '--'
        },
        lastPrice: Number.isFinite(o[2]) ? o[2] : '--',
        change: Number.isFinite(o[3]) ? o[3] : '--',
        high: Number.isFinite(o[4]) ? o[4] : '--',
        low: Number.isFinite(o[5]) ? o[5] : '--',
        volumn: Number.isFinite(o[6]) ? o[6] : '--'
    }))
    
    let props: any = {
        ...rest,
        buyData: coinMap.ETH,
        sellData: coinMap.LRC,
        tradeFloat: {priceDollar: +123, priceYuan: 2343232, percent: '+15%', timeUnit: "24h",
            volume: 112312312,
            floatTag:FloatTag.increase},

    }

    const onVisibleRowsChange=(data: any) => {
        console.log(data)
    }

    return <Box display={'flex'} flexDirection={'column'} flex={1}>

        <RowStyled container >
            <Grid item xs={3} >
                <MarketBlock {...{...props,tradeFloat: {priceDollar: 123,priceYuan: 2343232, percent: '0%', timeUnit: "24h",floatTag:FloatTag.none}}} />
            </Grid>
            <Grid item xs={3} >
                <MarketBlock {...{...props,tradeFloat: {priceDollar: 123,priceYuan: 2343232, percent: '-15%', timeUnit: "24h",floatTag:FloatTag.decrease}}} />
            </Grid>
            <Grid item xs={3} >
                <MarketBlock {...{...props}}></MarketBlock>
            </Grid>
            <Grid item xs={3} >
                <MarketBlock {...{...props}}></MarketBlock>
            </Grid>
        </RowStyled>
        <TableWrapStyled container marginY={3} paddingY={2} flex={1}>
            <Grid item xs={12} display={'flex'}>
                <QuoteTable onVisibleRowsChange={onVisibleRowsChange} rawData={formattedRawData} {...{ ...rest }} />
            </Grid>
        </TableWrapStyled>
    </Box>


})

export default QuotePage
