export const makeMarketArray = (coinKey: any, marketTrades: MarketTradeInfo[]) => {

    const tradeArray: Array<Partial<RawDataTradeItem>> = []

    marketTrades.forEach((item: MarketTradeInfo) => {
        try {
            const {base, quote} = getBaseQuote(item.market)
            const {forex} = store.getState().system
            const {currency} = store.getState().settings
            const {tokenMap} = store.getState().tokenMap
            if (tokenMap) {
                const baseToken = tokenMap[ base as string ]
                const quoteToken = tokenMap[ quote as string ]

                // console.log('item.volume:', item.volume, ' base:', baseToken)

                tradeArray.push({
                    side: item.side === Side.Sell ? TradeTypes.Sell : TradeTypes.Buy,
                    amount: {
                        from: {
                            key: base as string,
                            value: fm.toBig(item.volume).div(BIG10.pow(baseToken.decimals)).toNumber(),
                        },
                        to: {
                            key: quote as string,
                            value: fm.toBig(item.price).times(item.volume).div(BIG10.pow(quoteToken.decimals)).toNumber(),
                        },
                    },
                    price: {
                        key: '',
                        value: fm.toBig(item.price).toNumber(),
                    },
                    fee: {
                        key: '',
                        value: fm.toBig(item.fee).div(BIG10.pow(quoteToken.decimals)).toNumber(),
                    },
                    time: item.tradeTime,
                })
            }
        } catch (error) {
            //CATCHERROR:
            console.log(error)
            // new CustomError()
        }

    })
    // console.log('tradeArray:', tradeArray)
    return tradeArray;

}


export const useSwapPage = <C extends { [ key: string ]: any }>() => {
    /*** api prepare ***/
    const exchangeApi = exchangeAPI();
    const match: any = useRouteMatch(":symbol")
    const {coinMap, tokenMap, marketArray, marketCoins} = useTokenMap()
    const {ammMap} = useAmmMap();
    const {setShowConnect, setShowAccountInfo} = useOpenModals();
    const {ShowDeposit} = useModals()
    const {account} = useAccount()

    const walletLayer2State = useWalletLayer2();

    const [tradeData, setTradeData] = React.useState<SwapTradeData<IBData<C>> | undefined>(undefined);
    const [tradeCalcData, setTradeCalcData] = React.useState<TradeCalcData<C> | undefined>(undefined);
    const [tradeArray, setTradeArray] = React.useState<RawDataTradeItem[]>([]);
    const [tradeFloat, setTradeFloat] = React.useState<TradeFloat | undefined>(undefined);
    const [pair, setPair] = React.useState<{ coinAInfo: CoinInfo<C> | undefined, coinBInfo: CoinInfo<C> | undefined }>({
        coinAInfo: undefined,
        coinBInfo: undefined,
    });
    // const [market, setMarket] = React.useState<string>('')
    const [swapBtnI18nKey, setSwapBtnI18nKey] = React.useState<string>('swapBtn');


    //HIGH: get Router info
    // const symbol = match?.params.symbol ?? undefined;
    React.useEffect(() => {

        const symbol = match?.params.symbol ?? undefined;
        resetSwap(symbol)
    }, []);
    //TODO tickMap
    React.useEffect(() => {
    }, [])

    //HIGH: effect by wallet state update
    React.useEffect(() => {
        switch (walletLayer2State.status) {
            case "ERROR":
                walletLayer2State.statusUnset();
                // setState('ERROR')
                //TODO: show error at button page show error  some retry dispat again
                break;
            case "DONE":
                walletLayer2State.statusUnset();
                const {walletMap} = updateWallet();
                if (walletMap && tradeCalcData) {
                    setTradeCalcData({...tradeCalcData, walletMap} as TradeCalcData<C>);
                    setTradeData({
                        sell: {
                            belong: tradeCalcData.sellCoinInfoMap ? tradeCalcData.sellCoinInfoMap[ tradeCalcData.coinSell ]?.simpleName : undefined,
                            balance: walletMap ? walletMap[ tradeCalcData.coinSell ]?.count : 0
                        },
                        // @ts-ignore
                        buy: {
                            belong: tradeCalcData.sellCoinInfoMap ? tradeCalcData.sellCoinInfoMap[ tradeCalcData.coinBuy ]?.simpleName : undefined,
                            balance: walletMap ? walletMap[ tradeCalcData.coinBuy ]?.count : 0
                        },
                    } as SwapTradeData<IBData<C>>)
                }
                break;
            default:
                break;

        }
    }, [walletLayer2State.status])

    React.useEffect(() => {
    }, [])

    const btnArray: { [ key: number ]: [fn: (props: any) => any, args?: object] } = {
        [ fnType.UNCONNNECTED ]: [
            function () {
                setSwapBtnI18nKey('labelConnectWallet')
            }
        ]
        , [ fnType.DEFAULT ]: [
            function () {
                setSwapBtnI18nKey('depositTitleAndActive')
            }
        ]
        , [ fnType.ACTIVATED ]: [
            function () {
                setSwapBtnI18nKey('swapBtn')
            }
        ]
        , [ fnType.LOCKED ]: [
            function () {
                setSwapBtnI18nKey('labelUnLock')
            }
        ]
    };
    useCustomDCEffect(() => {
        accountStaticCallBack(btnArray)
    }, [account.status, btnArray])


    const swapBtnClickArray: { [ key: number ]: [fn: (props: any) => any, args?: object] } = {
        [ fnType.UNCONNNECTED ]: [
            function () {
                setShowConnect({isShow: true})
            }
        ]
        , [ fnType.DEFAULT ]: [
            function () {
                ShowDeposit(true)
            }
        ]
        , [ fnType.ACTIVATED ]: [
            function () {
                setSwapBtnI18nKey('swapBtn')
            }
        ]
        , [ fnType.LOCKED ]: [
            function () {
                setShowAccountInfo({isShow: true})
            }
        ]
    };
    const onSwapClick = ({sell, buy, slippage}: SwapTradeData<IBData<C>>) => {
        accountStaticCallBack(swapBtnClickArray)
        // onSwap()
    }
    const handleSwapPanelEvent = async (swapData: SwapData<SwapTradeData<IBData<C>>>, switchType: any): Promise<void> => {
        //TODO setMarket(market);

        const {tradeData} = swapData
        return new Promise((resolve) => {
            switch (switchType) {
                case SwapType.SEll_CLICK:
                    break
                case SwapType.BUY_CLICK:
                    break
                case SwapType.SELL_SELECTED:
                    resetSwap(`${tradeData.sell.belong}-${tradeData.buy.belong}`, 'sell', tradeData)
                    break
                case SwapType.BUY_SELECTED:
                    resetSwap(`${tradeData.sell.belong}-${tradeData.buy.belong}`, 'buy', tradeData)
                    break
                case SwapType.EXCHANGE_CLICK:
                    resetSwap(`${tradeData.sell.belong}-${tradeData.buy.belong}`)
                    break
                default:
                    break
            }

            resolve(undefined)
        })
    }
    const calculateTradeData = (type: 'sell' | 'buy', _tradeData: SwapTradeData<IBData<C>>): { _tradeCalcData: TradeCalcData<C>, _tradeData: SwapTradeData<IBData<C>> } => {
        //if(`${pair.coinAInfo?.simpleName}-${pair.coinBInfo?.simpleName}` === coinKey)
        if (_tradeData[ type ].tradeValue && tradeCalcData) {
            type === 'sell' ? _tradeData[ 'buy' ].tradeValue = fm.toBig(_tradeData[ 'sell' ].tradeValue).times(tradeCalcData.StoB).toNumber()
                : _tradeData[ 'sell' ].tradeValue = fm.toBig(_tradeData[ 'buy' ].tradeValue).times(tradeCalcData.BtoS).toNumber()
        }
        //TODO: renew  tradeCalcData
        let _tradeCalcData = {...tradeCalcData} as TradeCalcData<C>;
        return {_tradeData, _tradeCalcData}
    }

    const resetSwap = async (coinKey: any, type?: 'sell' | 'buy', _tradeData?: SwapTradeData<IBData<C>>) => {
        if (tradeCalcData
            && coinKey === `${tradeCalcData.coinSell}-${tradeCalcData.coinBuy}`
            && _tradeData
            && type
            && (!tradeData || (tradeData[ type ].tradeValue !== _tradeData[ type ].tradeValue))) {
            const {_tradeData: td, _tradeCalcData} = calculateTradeData(type, _tradeData)
            setTradeData(td)
            setTradeCalcData(_tradeCalcData)

        } else {
            let _tradeCalcData: Partial<TradeCalcData<C>> = {}
            let _tradeFloat: Partial<TradeFloat> = {}
            let _tradeArray: Array<Partial<RawDataTradeItem>> | undefined = undefined;
            _tradeCalcData = coinPairInit({coinKey, _tradeCalcData, tokenMap, coinMap})
            if (walletLayer2State.walletLayer2) {
                const {walletMap} = updateWallet();
                _tradeCalcData.walletMap = walletMap;
            }
            setTradeCalcData({...tradeCalcData, ..._tradeCalcData} as TradeCalcData<C>);

            let {
                amm,
                market
            } = getExistedMarket(marketArray, _tradeCalcData.coinSell as string, _tradeCalcData.coinBuy as string);
            let apiList = [];
            //TODO wallet saga done
            if (marketArray && amm && market && ammMap) {
                // let pairPromise =  usePairTitleBlock({market})
                apiList = [
                    exchangeApi.getMarketTrades({market}),
                    pairTitleBlock({coinKey: market, ammKey: amm, ammMap})
                ];
                //HiGH: this need add websocket to update infr ticker ammpoolsbalace
                // @ts-ignore
                Promise.all([...apiList]).then(
                    ([{marketTrades}, {ammpoolsbalance, tickMap}]: any[]) => {
                        if (tokenMap) {
                            let {_tradeCalcData: _td, _pair} = pairTitleDone({
                                market,
                                ammpoolsbalance,
                                tokenMap,
                                _tradeCalcData,
                                coinMap,
                                marketCoins
                            })
                            _tradeCalcData = _td;
                            _tradeFloat = makeTickView(tickMap[ market ])
                            _tradeArray = makeMarketArray(market, marketTrades)
                            // @ts-ignore
                            setTradeCalcData(_tradeCalcData as TradeCalcData<C>);
                            // @ts-ignore
                            setTradeFloat(_tradeFloat);
                            setTradeArray(_tradeArray as RawDataTradeItem[])
                            setPair(_pair)
                            setTradeData({
                                sell: {
                                    belong: _tradeCalcData.sellCoinInfoMap ? _tradeCalcData.sellCoinInfoMap[ _tradeCalcData.coinSell ]?.simpleName : undefined,
                                    balance: _tradeCalcData.walletMap ? _tradeCalcData.walletMap[ _tradeCalcData.coinSell ]?.count : 0
                                },
                                // @ts-ignore
                                buy: {
                                    belong: _tradeCalcData.sellCoinInfoMap ? _tradeCalcData.sellCoinInfoMap[ _tradeCalcData.coinBuy ]?.simpleName : undefined,
                                    balance: _tradeCalcData.walletMap ? _tradeCalcData.walletMap[ _tradeCalcData.coinBuy ]?.count : 0
                                },
                            } as SwapTradeData<IBData<C>>)
                        }


                    }).catch((error) => {
                    throw new CustomError({...ErrorMap.TRADE_LITE_SET_PAIR_ERROR, options: error})
                    //TODO solve error
                })

            }
        }

    }
    return {
        tradeCalcData,
        tradeFloat,
        tradeArray,
        tradeData,
        pair,
        onSwapClick,
        swapBtnI18nKey,
        handleSwapPanelEvent
    }

}
