import { all, call, delay, fork, put, take, takeLatest } from "redux-saga/effects"
import { updateSystem, getSystemStatus, updateRealTimeObj } from './reducer'
import { ENV, NETWORK, NETWORKEXTEND } from "./interface"
import store from '../index';
import { reset } from '../account/reducer';
import { ChainId } from 'loopring-sdk';
import { ammpoolAPI, exchangeAPI } from '../apis/api';
import { getAmmMap, updateRealTimeAmmMap } from '../Amm/AmmMap';
import { getTokenMap, statusUnset as tokenStatusUnset } from '../token';


const initConfig  = function* <R extends {[key:string]:any}>() {
    store.dispatch(reset(undefined));
    const exchangeApi = exchangeAPI();
    const ammpoolApi = ammpoolAPI();
    const {tokenSymbolMap: tokensMap} = yield call(async ()=> await exchangeApi.getTokens())
    const {ammpools} = yield call(async ()=>await ammpoolApi.getAmmPoolConf());
    const {pairs,marketArr,tokenArr}  = yield call(async ()=>await exchangeApi.getMixMarkets());
    store.dispatch(getTokenMap({tokensMap,pairs,marketArr,tokenArr}))
    yield take('tokenMap/getTokenMapStatus');
    store.dispatch(getAmmMap({ammpools}))
    if(store.getState().tokenMap.status === 'ERROR'){

    }
    // yield take('tokenMap/getTokenMapStatus');

}

const getSystemsApi = async <R extends {[key:string]:any}>(chainId:any)=> {
    //TODO get some other reuqired id...... put into system
    // const { chainId } = system
    const env =  window.location.hostname === 'localhost'? ENV.DEV :ChainId.GORLI === chainId ?  ENV.UAT : ENV.PROD
    chainId = ChainId.GORLI === chainId ? ChainId.GORLI: ChainId.MAINNET === chainId ?  ChainId.MAINNET: NETWORKEXTEND.NONETWORK
    const faitPrices = (await exchangeAPI().getFiatPrice({ legal: 'CNY' })).fiatPrices
    const gasPrice = (await exchangeAPI().getGasPrice()).gasPrice / 1e+9
    const baseURL = env === ENV.PROD ? 'https://api3.loopring.io' : 'https://uat2.loopring.io';
    const forex = faitPrices['USDC']
    let {__timer__} = store.getState().system;
    __timer__ = ((__timer__)=>{
        if(__timer__ && __timer__ !== -1){
            clearInterval(__timer__)

        }
        return  setInterval(async()=>{
            const faitPrices = (await exchangeAPI().getFiatPrice({ legal: 'CNY' })).fiatPrices
            const gasPrice = (await exchangeAPI().getGasPrice()).gasPrice / 1e+9
            const forex = faitPrices['USDT']
            store.dispatch(updateRealTimeAmmMap(undefined))
            store.dispatch(updateRealTimeObj({faitPrices, gasPrice,forex}))
        },900000)   //

    })(__timer__);

    return {  chainId, env, baseURL, faitPrices, gasPrice, forex , __timer__ }
}

export function* getUpdateSystem({payload}:any) {
    try {
        // @ts-ignore
        const { chainId } = payload;
        // console.log('getPostsSaga systemKey',systemKey, systemKeys)
        const {   env, baseURL, faitPrices, gasPrice, forex , __timer__ } = yield call(getSystemsApi, chainId);

        yield put(getSystemStatus({   env, baseURL, faitPrices, gasPrice, forex , __timer__ }));
        yield call(initConfig)
        //TODO check wallect store
    } catch (err) {
        yield put(getSystemStatus(err));
    }
}

function* systemSaga() {
    yield all([takeLatest(updateSystem, getUpdateSystem)]);
}
function* initConfigSaga(){
    //yield all([takeLatest(initConfig, getUpdateSystem)]);
}


export const systemForks = [
    fork(systemSaga),
 //   fork(initConfig),
]
