import React from 'react';
import { useAmmMap } from '../../../stores/Amm/AmmMap';
import {
    CoinInfo,
    CustomError,
    ErrorMap,
} from '@loopring-web/component-lib/src/static-resource';
import {  useTicker } from '../../../stores/ticker';
import { PoolsRow } from '@loopring-web/component-lib';
import { deepClone } from '../../../utils/obj_tools';
import { STATUS } from '../../../stores/constant';
import { useAmmActivityMap } from '../../../stores/Amm/AmmActivityMap';
import { AmmCardProps } from '@loopring-web/component-lib/static-resource';
import { tokenMapSlice, useTokenMap } from '../../../stores/token';

export function useAmmMiningUI<R extends {[key:string]:any},I>():{ammActivities:Array<AmmCardProps<I>>} {
    const [data,setData] = React.useState<Array<AmmCardProps<I>>>([]);
    const {
        ammMap,
        status: ammMapStatus,
        errorMessage: errorAmmMap,
        statusUnset:ammMapStatusUnset,
        updateAmmMap,
    } = useAmmMap();
    const {coinMap} = useTokenMap();
    const {
        ammActivityMap,
        status:  ammActivityStatus,
        errorMessage: errorAmmActivityMap,
        statusUnset:  ammActivityStatusUnset ,
        updateAmmActivityMap,
    } = useAmmActivityMap();
    const {
        tickerMap,
        status: tickerStatus,
        errorMessage: errorTickerMap,
        statusUnset: tickerStatusUnset ,
        updateTickers,
    } = useTicker();
    const updateRawData = React.useCallback(()=>{
        try {
            const _ammActivityMap:PoolsRow<I>  = deepClone(ammActivityMap);
            let mapArray = [];
            for (let key in  ammActivityMap) {
                if (ammMap && coinMap) {
                    _ammActivityMap[ key ] = {
                        // @ts-ignore
                        ...deepClone(ammMap[ key ]),
                        coinAInfo: coinMap[ ammMap[ key ].coinA as string ],
                        coinBInfo: coinMap[ ammMap[ key ].coinB as string ],
                        activity: _ammActivityMap[ key ],
                        tradeFloat: tickerMap && tickerMap[ key ]
                    }
                    mapArray.push(_ammActivityMap[key])
                }

            }
            setData(mapArray);
        }catch (error){
            throw new CustomError({...ErrorMap.NO_TOKEN_MAP,options:error})
        }
    }, [ammActivityMap, tickerMap, ammMap])

    const updateTickersUI = React.useCallback(()=>  {
            if(ammActivityMap && Object.keys(ammActivityMap).length>0){
                updateTickers(Object.keys(ammActivityMap));
            }
    },[ammActivityMap,updateTickers])
    React.useEffect(() => {
        switch (tickerStatus) {
            case "ERROR":
                console.log("ERROR",'get ticker error,ui');
                tickerStatusUnset()
                break;
            case "PENDING":
                break;
            case "DONE":
                tickerStatusUnset()
                updateRawData()
                break;
            default:
                break;
        }
    }, [tickerStatus, tickerStatusUnset, updateRawData]);
    React.useEffect(() => {
        switch (ammActivityStatus) {
            case "ERROR":
                console.log("ERROR",'get ammMap error,ui');
                ammActivityStatusUnset();
                break;
            case STATUS.PENDING:
                console.log("PENDING",'get ammMap error,ui');
                // ammMapStatusUnset();
                break;
            case STATUS.DONE:
                if(ammMapStatus !== STATUS.PENDING) {
                    ammActivityStatusUnset();
                    ammMapStatusUnset();
                    updateTickersUI();
                }
                break;
            default :
                break;
        }
    }, [ammMapStatus, ammActivityStatus, ammActivityStatusUnset, ammMapStatusUnset, updateTickersUI]);
    // init AmmMap at begin
    React.useEffect(() => {
        if (!ammMap || Object.keys(ammMap).length === 0) {
            updateAmmMap();
        }
        if(!ammActivityMap || Object.keys(ammActivityMap).length === 0){
            updateAmmActivityMap();
        } else {
            updateTickersUI();
        }
    },[]);




    return {
        ammActivities:data,
        // updateTickersUI,
    }
}

