import { createSlice, PayloadAction, Slice } from '@reduxjs/toolkit'
import { ENV, SystemStatus } from './interface';
import { STATUS } from '../constant';

const initialState: SystemStatus = {
    env: ENV.PROD,
    chainId: 'unknown',
    // network:'NONETWORK',
    baseURL: '',
    faitPrices: undefined,
    gasPrice: undefined,
    forex: undefined,
    __timer__: -1,
    status: 'UNSET',
    errorMessage: null,
}
const systemSlice: Slice<SystemStatus> = createSlice({
    name: 'system',
    initialState,
    reducers: {
        updateSystem(state, action: PayloadAction<Partial<{ chainId: 1 | 5 | 'unknown' }>>) {
            console.log(action.type)
            state.status = STATUS.PENDING
        },
        updateRealTimeObj(state, action: PayloadAction<Partial<{ faitPrices: any, gasPrice: number, forex: number }>>) {
            const {forex, faitPrices, gasPrice} = action.payload;
            if (forex) {
                state.forex = forex
            }
            if (faitPrices) {
                state.faitPrices = faitPrices
            }
            if (gasPrice) {
                state.gasPrice = gasPrice
            }


            // state = {
            //     ...state.system,
            //     forex: forex?,
            //     faitPrices: action.payload.faitPrices ? action.payload.faitPrices : state.system.faitPrices,
            //     gasPrice: action.payload.gasPrice ? action.payload.gasPrice : state.system.gasPrice,
            // };

        },
        getSystemStatus(state, action: PayloadAction<Partial<SystemStatus>>) {

            // @ts-ignore
            if (action.error) {
                state.status = STATUS.ERROR
                // @ts-ignore
                state.errorMessage = action.error
            }
            const {env, baseURL, faitPrices, gasPrice, forex, __timer__} = action.payload;
            if (env) {
                state.env = env
            }
            if (baseURL) {
                state.baseURL = baseURL
            }
            if (faitPrices) {
                state.faitPrices = faitPrices
            }
            if (gasPrice) {
                state.gasPrice = gasPrice
            }
            if (forex) {
                state.forex = forex
            }
            if (__timer__) {
                state.__timer__ = __timer__
            }
            state.status = STATUS.DONE
        },
        statusUnset: state => {
            state.status = STATUS.UNSET
        }

    },
});
export { systemSlice };
export const {updateSystem, getSystemStatus, statusUnset, updateRealTimeObj} = systemSlice.actions;