import React from 'react'
import { TradeTable } from '@loopring-web/component-lib'
import { WithTranslation, withTranslation } from 'react-i18next'
import styled from '@emotion/styled'
import { Box, Paper } from '@material-ui/core'
import { useGetTxs } from './hooks'

const StylePaper = styled(Box)`
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  flex: 1;
  background-color: ${({ theme }) => theme.colorBase.background().default};
  border-radius: ${({ theme }) => theme.unit}px;
  padding: 20px;

  .title {
    font-family: Gilroy-Medium;
    font-size: ${({ theme }) => theme.unit * 3}px;
    line-height: 19px;
  }

  .tableWrapper {
    display: flex;
    flex: 1;
    margin-top: 20px;
    border: 1px solid ${({ theme }) => theme.colorBase.borderColor};
    border-radius: ${({ theme }) => theme.unit}px;
    padding: 26px;

    .rdg {
      flex: 1;
    }
  }
` as typeof Paper;

const TradePanel = withTranslation('common')((rest:WithTranslation<'common'>) => {
    const container = React.useRef(null);
    const [pageSize, setPageSize] = React.useState(10);
    React.useEffect(() => {
        // @ts-ignore
        let height = container?.current?.offsetHeight;
        if (height) {
            setPageSize(Math.floor((height - 120) / 44) - 2);
        }
    }, [container, pageSize]);

    const { txs } = useGetTxs()

    const txList = txs !== undefined ? txs: []

    return (
        <>
            <StylePaper ref={container}>
                <div className="title">Trades</div>
                <div className="tableWrapper">
                    <TradeTable {...{
                      // rawData: txList,
                      rawData: [],
                      pagination: {
                        pageSize: pageSize
                      },
                      ...rest}}/>
                </div>
            </StylePaper>
        </>
    )
})

export default TradePanel
