import { RefAttributes, useState } from 'react'

import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Checkbox from '@material-ui/core/Checkbox'
import styled from '@emotion/styled'

import { FormControlLabel, TradeTable, } from '@loopring-web/component-lib'
import { withTranslation, WithTranslation } from 'react-i18next';
import { CheckBoxIcon, CheckedIcon } from '@loopring-web/component-lib/static-resource'
import { RouteComponentProps, withRouter } from 'react-router'
import { Box } from '@material-ui/core';
import { RawDataTradeItem } from '@loopring-web/component-lib/components/tableList/tradeTable/TradeTable';
import { BasicHeaderItem, HeadMenuType } from '@loopring-web/component-lib/components/basic-lib/lists/Interface';

const StylePaper = styled(Box)`
  width: 100%;
  height: 100%;
  flex: 1;
  background-color: ${({theme}) => theme.colorBase.background().default};
  border-radius: ${({theme}) => theme.unit}px;
  padding: 20px;

  .tableWrapper {
    margin-top: ${({theme}) => theme.unit * 1.5}px;
    border: 1px solid ${({theme}) => theme.colorBase.borderColor};
    border-radius: ${({theme}) => theme.unit}px;
    padding: 26px;
  }
` as typeof Box;


const applyProps = (index: number) => {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    }
}
const StyledTabsWrapper = styled.div`
          position: relative;
          width: 100%;
          margin-top: ${({theme}) => theme.unit * 4}px;
        `

const StyledFormControlLabel = styled(FormControlLabel)`
          position: absolute;
          top: 0;
          right: ${({theme}) => theme.unit}px;
          margin: ${({theme}) => theme.unit}px 0 0 0;
        `


const TradePanel = withTranslation('common')(
   // withRouter(
    (
        {tradeArray,  t}:
            { tradeArray: RawDataTradeItem[] } & WithTranslation & RouteComponentProps) => {
        const [isAllTrade, setIsAllTrade] = useState(false)
        const [value, setValue] = useState(0)
        const handleChange = (event: any, newValue: any) => {
            setValue(newValue)
        }

        const handleCheckboxChange = () => {
            setIsAllTrade((flag: boolean) => !flag)
        }

        return (
            <StyledTabsWrapper>
                <StyledFormControlLabel
                    control={<Checkbox checked={isAllTrade} size={'small'} checkedIcon={<CheckedIcon/>}
                                       icon={<CheckBoxIcon/>} color={'default'} onChange={handleCheckboxChange}/>}
                    label={t('labelTradePanelHideOtherPairs')}/>

                <Tabs value={value}
                      onChange={handleChange}
                      aria-label="tabs switch">
                    <Tab label={t('labelMyTrade')} {...applyProps(0)} />
                    <Tab label={t('labelRecent')}   {...applyProps(1)} />
                </Tabs>

                <StylePaper marginTop={2}>
                    <TradeTable rawData={tradeArray}/>
                </StylePaper>

            </StyledTabsWrapper>
        )
    }
    ) as  (props: { tradeArray: RawDataTradeItem[] }) => JSX.Element;
//)

export default TradePanel

