import { AmmMap } from '../../stores/Amm/AmmMap';
import { getBaseQuote, LoopringMap, TickerData } from 'loopring-sdk';
import { ammpoolAPI, exchangeAPI } from '../../stores/apis/api';
import * as fm from 'loopring-sdk';
import { BIG10 } from '../../defs/swap_defs';
import { CoinMap } from '@loopring-web/component-lib/static-resource/index';

export const pairTitleBlock = <C extends { [ key: string ]: any }, I extends { [ key: string ]: any }>({
                                                                                                           coinKey,
                                                                                                           ammKey,
                                                                                                           ammMap
                                                                                                       }: { coinKey: string, ammKey: string, ammMap: AmmMap<C, I> }): Promise<{
    ammpoolsbalance: any, tickMap: LoopringMap<TickerData>
}> => {
    const exchangeApi = exchangeAPI();
    const ammpoolApi = ammpoolAPI();
    return new Promise((resolve, reject) => {
        Promise.all([
            ammpoolApi.getAmmPoolSnapshot({poolAddress: ammMap[ ammKey ].address}),
            exchangeApi.getTicker({market: coinKey})])
            // exchangeApi.getMarketTrades({market:coinKey})])
            .then(([{raw_data}, {tickMap}]) => {
                resolve({
                    ammpoolsbalance: raw_data,
                    tickMap,
                })
            })
    })
}

export const pairTitleDone = <C>({market, ammpoolsbalance, tokenMap, _tradeCalcData, coinMap, marketCoins}:any)=>{
    const {base, quote} = getBaseQuote(market)
    let stob = fm.toBig(Number(ammpoolsbalance.pooled[ 0 ].volume)).div(BIG10.pow( tokenMap[base as string].decimals))
        .div(Number(ammpoolsbalance.pooled[ 1 ].volume)).div(BIG10.pow( tokenMap[quote as string].decimals)).toNumber()
    if (market !== `${_tradeCalcData.coinSell}-${_tradeCalcData.coinBuy}`) {
        stob = 1 / (stob * 1.0);
    }
    _tradeCalcData.StoB = stob;
    _tradeCalcData.BtoS = 1 / (stob * 1.0);
    _tradeCalcData.sellCoinInfoMap = coinMap && marketCoins?.reduce((prev: any, item: string | number) => {
        return {...prev, [ item ]: coinMap[ item ]}
    }, {} as CoinMap<C>)
    _tradeCalcData.buyCoinInfoMap = coinMap && tokenMap && tokenMap[ _tradeCalcData.coinSell as string ].tradePairs?.reduce((prev: any, item: string | number) => {
        return {...prev, [ item ]: coinMap[ item ]}
    }, {} as CoinMap<C>);

    return {
        _pair:{
            coinAInfo:  _tradeCalcData.sellCoinInfoMap? _tradeCalcData.sellCoinInfoMap[ base as string ] : undefined,
            coinBInfo: _tradeCalcData.sellCoinInfoMap? _tradeCalcData.sellCoinInfoMap[ quote as string ] : undefined,
        },
        _tradeCalcData
    }
    //setPair();
}