import { AmmpoolAPI, ChainId, ExchangeAPI, UserAPI, WsAPI } from "loopring-sdk"
import { getChainId } from 'stores/trading/hook'

export const userAPI = () => {

    return new UserAPI(getChainId() as ChainId)
}

export const exchangeAPI = () => {
    return new ExchangeAPI(getChainId() as ChainId)
}

export const ammpoolAPI = () => {
    return new AmmpoolAPI(getChainId() as ChainId)
}

export const wsAPI = () => {
    return new WsAPI(getChainId() as ChainId)
}
